import uCharts from '@/components/u-charts/u-charts.js';
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item-1/uni-list-item.vue'
import uniCollapse from "@/components/uni-collapse/uni-collapse.vue"
import uniCollapseItem from "@/components/uni-collapse-item-1/uni-collapse-item.vue"
//环形图
var _self;
var canvaRing=null;

export default {
	components: {uniList,uniListItem,uniCollapse,uniCollapseItem},
	data() {
		return {
			//面板数据
			cash: '0.00',
			begin: '0.99',
			end: '0.00',
			
			//环形
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			serverData:''
		};
	},
	onLoad() {
		_self = this;
		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		change(e) {
			console.log(e);
		},
		//请求图标要使用的数据
		getServerData(){
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data:{
				},
				success: function(res) {
					console.log(res.data.data)
					let Ring={series:[]};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					Ring.series=res.data.data.Ring.series;
					_self.showRing("canvasRing",Ring);
				},
				fail: () => {
					_self.tips="网络错误，小程序端请检查合法域名";
				},
			});
		},
		showRing(canvasId,chartData){//请求完数据 回调函数 再调用这个展示
			canvaRing=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'ring',
				fontSize:11,
				legend:true,
				title: {
					name: '70%',
					color: '#7cb5ec',
					fontSize: 25*_self.pixelRatio,
					offsetY:-20*_self.pixelRatio,
				},
				subtitle: {
					name: '总计',
					color: '#666666',
					fontSize: 15*_self.pixelRatio,
					offsetY:30*_self.pixelRatio,
				},
				extra: {
					pie: {
					  offsetAngle: -45,
					  ringWidth: 40*_self.pixelRatio,
					  labelWidth:15
					}
				},
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				series: chartData.series,
				animation: true,
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				disablePieStroke: true,
				dataLabel: true,
			});
		},
		touchRing(e){//实现触碰变大效果 
			canvaRing.showToolTip(e, {
				format: function (item) {
					return item.name + ':' + item.data 
				}
			});
		},
	}
}
